import React, { useContext, useState, useEffect } from "react";
import {
  Autocomplete,
  Avatar,
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  Typography,
  Divider,
  TextField,
  useTheme,
} from "@mui/material";

import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import _ from "lodash";
import Swal from "sweetalert2";
import { Formik } from "formik";
import { object, string, date, number } from "yup";
import { useMutation } from "react-query";
import { Edit } from "@mui/icons-material";
import { LoadingButton } from "@mui/lab";
import { updateStudent } from "../api/studentApi";
import { CustomContext } from "../context/provider/CustomProvider";

function EditStudent({ open, setOpen }) {
  const { customState } = useContext(CustomContext);
  const { palette } = useTheme();

  const levelOptions = ["100", "200", "300", "400"];

  const [depart, setDepart] = useState([]);
  const [prog, setProg] = useState([]);

  const [id, setId] = useState("");
  const [profileImg, setProfileImg] = useState(null);
  const [fullname, setFullname] = useState("");
  const [gender, setGender] = useState("Male");
  const [dob, setDob] = useState("");
  const [email, setEmail] = useState("");
  const [address, setAddress] = useState("");
  const [telephoneNo, setTelephoneNo] = useState("");
  const [department, setDepartment] = useState(null);
  const [programme, setProgramme] = useState(null);
  const [level, setLevel] = useState(null);

  useEffect(() => {
    let departOptions = [];
    let progOptions = [];

    customState.departmentOptions.forEach((data, index) => {
      departOptions.push(data.department);
    });
    customState.programmeOptions.forEach((data, index) => {
      progOptions.push(data.programme);
    });

    setDepart(departOptions);
    setProg(progOptions);
  }, [customState.programmeOptions, customState.departmentOptions]);

  useEffect(() => {
    if (!_.isEmpty(customState.studentEditData)) {
      setId(customState?.studentEditData?._id);
      setProfileImg(customState?.studentEditData?.profile);
      setFullname(customState?.studentEditData?.fullname);
      setGender(customState?.studentEditData?.gender);
      setDob(customState?.studentEditData?.dob);
      setEmail(customState?.studentEditData?.email);
      setAddress(customState?.studentEditData?.address);
      setTelephoneNo(customState?.studentEditData?.telephoneNo);
      setDepartment(customState?.studentEditData?.department);
      setProgramme(customState?.studentEditData?.programme);
      setLevel(customState?.studentEditData?.level);
    }
  }, [customState.studentEditData]);

  const validationSchema = object().shape({
    fullname: string().trim().required("required*"),
    gender: string().trim().required("required*"),
    dob: date().required("required*"),
    email: string().trim().required("required").email("Invalid email address"),
    address: string().trim().required("required"),
    telephoneNo: number().required("required*"),
    department: string().trim().required("required*").nullable(true),
    programme: string().trim().required("required*").nullable(true),
    level: string().required("required*").nullable(true),
  });

  const studentMutate = useMutation(updateStudent);

  const onSubmit = (values, options) => {
    // console.log(values);

    options.setSubmitting(true);
    studentMutate.mutateAsync(values, {
      onSuccess: (data) => {
        Swal.fire({
          icon: "success",
          iconColor: palette.primary.main,
          title: "Success",
          text: "Student saved successfully!!!",
          confirmButtonColor: "#5aa7a7",
        });
        options.resetForm();
      },
      onError: (v, error, cb) => {
        console.log(error);
      },
      onSettled: () => {
        options.setSubmitting(false);
      },
    });
  };
  return (
    <Dialog maxWidth="md" fullWidth open={open} onClose={() => setOpen(false)}>
      <DialogTitle title="Add Student">
        <Edit /> Edit Student
      </DialogTitle>
      <Formik
        initialValues={{
          _id: id,
          profile: profileImg,
          fullname,
          gender,
          dob,
          email,
          address,
          telephoneNo,
          department,
          programme,
          level,
        }}
        validationSchema={validationSchema}
        onSubmit={onSubmit}
        enableReinitialize={true}
      >
        {({
          isSubmitting,
          values,
          errors,
          touched,

          handleSubmit,
        }) => (
          <>
            <DialogContent>
              <Box
                sx={{
                  width: 120,
                  height: 120,
                  backgroundColor: "#fff",
                  position: "relative",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  margin: "auto",
                }}
              >
                <Avatar
                  src={
                    customState?.studentEditData?.profile === "" ||
                    customState?.studentEditData?.profile === undefined ||
                    customState?.studentEditData?.profile === null
                      ? null
                      : `${process.env.REACT_APP_BASE_URL}/images/${customState?.studentEditData?.profile}`
                  }
                  sx={{ width: 100, height: 100, position: "relative" }}
                />
                <FormLabel
                  htmlFor="profile"
                  title="Attach Your Photo"
                  sx={{
                    width: "30px",
                    height: "30px",
                    borderRadius: "50%",
                    padding: "5px",
                    display: "flex",
                    alignItems: "center",
                    position: "absolute",
                    top: 0,
                    right: 0,
                    backgroundColor: "#5aa7a7",
                  }}
                >
                  <Edit sx={{ width: 16, color: "#fff" }} />
                </FormLabel>
                <input
                  type="file"
                  hidden
                  id="profile"
                  // onChange={(e) => setProfileImg(e.target.files[0])}
                />
              </Box>
              <Typography fontWeight='bold'>1.Personal Information</Typography>
              <Divider />

              <Grid container spacing={3} paddingY={2}>
                <Grid item xs={12} md={6}>
                  <TextField
                    variant="filled"
                    label="FullName"
                    placeholder="FullName here"
                    id="fullname"
                    fullWidth
                    size="small"
                    value={fullname}
                    error={Boolean(touched.fullname && errors.fullname)}
                    helperText={errors.fullname}
                    onChange={(e) => setFullname(e.currentTarget.value)}
                    required
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    variant="filled"
                    // label="FullName"
                    placeholder=""
                    type="date"
                    fullWidth
                    size="small"
                    value={values.dob}
                    error={Boolean(touched.dob && errors.dob)}
                    helperText={errors.dob}
                    onChange={(e, value) => setDob(e.currentTarget.value)}
                    required
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <FormControl>
                    <FormLabel id="demo-controlled-radio-buttons-group">
                      Gender
                    </FormLabel>
                    <RadioGroup
                      aria-labelledby="demo-controlled-radio-buttons-group"
                      name="controlled-radio-buttons-group"
                      row
                      value={gender}
                      onChange={(e, value) => setGender(value)}
                    >
                      <FormControlLabel
                        value="Male"
                        control={<Radio />}
                        label="Male"
                      />
                      <FormControlLabel
                        value="Female"
                        control={<Radio />}
                        label="Female"
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                {/* <Grid item></Grid> */}
              </Grid>

              <Typography fontWeight='bold'>2.Contact Information</Typography>
              <Divider />
              <Grid container spacing={3} paddingY={2}>
                <Grid item xs={12} md={6}>
                  <TextField
                    variant="filled"
                    label="Email Address"
                    placeholder="Email Address here"
                    fullWidth
                    size="small"
                    value={email}
                    error={Boolean(touched.email && errors.email)}
                    helperText={errors.email}
                    onChange={(e) => setEmail(e.currentTarget.value)}
                    required
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    variant="filled"
                    label="Telephone"
                    placeholder="Telephone here"
                    fullWidth
                    size="small"
                    value={telephoneNo}
                    error={Boolean(touched.telephoneNo && errors.telephoneNo)}
                    helperText={errors.telephoneNo}
                    onChange={(e, value) =>
                      setTelephoneNo(e.currentTarget.value)
                    }
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    variant="filled"
                    label="Address"
                    placeholder="Address here"
                    multiline
                    maxRows={4}
                    fullWidth
                    size="small"
                    value={address}
                    error={Boolean(touched.address && errors.address)}
                    helperText={errors.address}
                    onChange={(e) => setAddress(e.currentTarget.value)}
                  />
                </Grid>
              </Grid>

              <Typography fontWeight='bold'>3.Academic Information</Typography>
              <Divider />
              <Grid container spacing={3} paddingY={2}>
                <Grid item xs={12} md={6}>
                  <Autocomplete
                    disablePortal
                    options={depart}
                    value={department}
                    onChange={(e, value) => setDepartment(value)}
                    renderInput={(props) => (
                      <TextField
                        variant="filled"
                        {...props}
                        label="Department"
                        fullWidth
                        size="small"
                        error={Boolean(touched.department && errors.department)}
                        helperText={errors.department}
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Autocomplete
                    disablePortal
                    options={prog}
                    value={programme}
                    onChange={(e, value) => setProgramme(value)}
                    renderInput={(props) => (
                      <TextField
                        variant="filled"
                        {...props}
                        label="Programme"
                        fullWidth
                        size="small"
                        error={Boolean(touched.programme && errors.programme)}
                        helperText={errors.programme}
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Autocomplete
                    disablePortal
                    options={levelOptions}
                    value={level}
                    onChange={(e, value) => setLevel(value)}
                    renderInput={(props) => (
                      <TextField
                        variant="filled"
                        {...props}
                        label="Level"
                        fullWidth
                        size="small"
                        error={Boolean(touched.level && errors.level)}
                        helperText={errors.level}
                      />
                    )}
                  />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <LoadingButton
                sx={{ color: "#fff", minWidth: 100 }}
                variant="contained"
                onClick={handleSubmit}
                loading={isSubmitting}
              >
                Save
              </LoadingButton>
              <Button color="secondary" onClick={() => setOpen(false)}>
                Cancel
              </Button>
            </DialogActions>
          </>
        )}
      </Formik>
    </Dialog>
  );
}

export default EditStudent;
