/* eslint-disable import/no-anonymous-default-export */
export const generateGradePoint = (grade) => {
  switch (grade) {
    case "A":
      return 12;
    case "B+":
      return 10.5;
    case "B":
      return 9;
    case "C+":
      return 7.5;
    case "C":
      return 6;
    case "D+":
      return 4.5;
    case "D":
      return 3;
    case "E":
      return 1.5;
    case "F":
      return 0;
    default:
      return "";
  }
};

export default { generateGradePoint };
