import React, { useState, useContext, useEffect } from "react";
import MaterialTable, { MTableToolbar } from "material-table";
import { ExportCsv, ExportPdf } from "@material-table/exporters";
import { Box, Avatar, Stack, ButtonGroup, Alert } from "@mui/material";
import { renderToStaticMarkup } from "react-dom/server";
import {
  DeleteSharp,
  Edit,
  MoreVertSharp,
  VisibilitySharp,
  NumbersSharp,
  DocumentScannerSharp,
  RefreshSharp,

} from "@mui/icons-material";
import { Menu, MenuList, MenuButton, MenuItem } from "@reach/menu-button";
import { useNavigate } from "react-router-dom";
import { tableIcons } from "../../assets/tableIcons";
import { CustomContext } from "../context/provider/CustomProvider";
import { useMutation } from "react-query";
import { deleteStudent } from "../api/studentApi";
import EditStudent from "../modals/EditStudent";
import AssignIndexNumber from "../modals/AssignIndexNumber";
import { publishAllResult, sendResults } from "../api/resultApi";
import PrintPage3 from "../../pages/PrintPage3";
import { LoadingButton } from "@mui/lab";



function CustomStudentTable({
  heading,
  isLoading,
  data,
  refresh,
  sortComponent,
}) {
  const navigate = useNavigate();

  const { customDispatch } = useContext(CustomContext);

  const [display, setDisplay] = useState("none");
  const [openStudentEdit, setOpenStudentEdit] = useState(false);
  const [openAssignIndex, setOpenAssignIndex] = useState(false);
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [generatedStudents, setGeneratedStudents] = useState([]);
  const [generateResultsLoader, setGenerateResultsLoader] = useState(false);
  const [publishResultsLoader, setPublishResultsLoader] = useState(false);

  const [alertMessage, setAlertMessage] = useState({
    severity: "",
    msg: "",
  });

  useEffect(() => {
    window.addEventListener("scroll", isSticky);
    return () => {
      window.removeEventListener("scroll", isSticky);
    };
  });

  /* Method that will fix header after a specific scrollable */
  const isSticky = (e) => {
    let d = "";
    const scrollTop = window.scrollY;
    if (scrollTop >= 340) {
      d = "block";
    } else if (scrollTop <= 370) {
      d = "none";
    } else {
      d = "none";
    }

    setDisplay(d);
  };

  // //Delete info
  let deleteStudentMutate = useMutation(deleteStudent);
  let generateResultsMutate = useMutation(publishAllResult);
  const sendResultsMutate = useMutation(sendResults);

  function deleteInfo(id) {
    deleteStudentMutate.mutateAsync(id, {
      onSuccess: (data) => {
        alert("Data deleted");
      },
    });
  }

  //Generate selected students results
  function generateResults(rowData) {
    setGenerateResultsLoader(true);
    const indexNumbers = rowData.map((student) => student.indexNumber);

    Promise.all(indexNumbers).then((indexNumber) => {
      generateResultsMutate.mutateAsync(indexNumber, {
        onSettled: () => {
          setGenerateResultsLoader(false);
        },
        onSuccess: (data) => {
          setGeneratedStudents(data);
          setAlertMessage({
            severity: "info",
            msg: `Results of ${data.length} students generated!!`,
          });
        },
      });
    });
  }

  //Send results through email
  const handlePublishResults = () => {
    setPublishResultsLoader(true);
    console.log(generatedStudents);
    const dataToEmail = generatedStudents.map((student) => {
      const dataToBePublished = renderToStaticMarkup(
        <PrintPage3
          studentInfo={student.student}
          studentRecord={student.result}
        />
      );

      return {
        indexNumber: student.student.indexNumber,
        email: student.student.email,
        htmlText: dataToBePublished,
      };
    });

    Promise.all(dataToEmail).then((emailInfo) => {
      sendResultsMutate.mutateAsync(emailInfo, {
        onSettled: () => {
          setPublishResultsLoader(false);
        },
        onSuccess: (data) => {
          setAlertMessage({
            severity: "success",
            msg: `Results of ${data.length} students published!!`,
          });
          setSelectedStudents([]);
          setGeneratedStudents([]);
        },
      });
    });
  };

  const handleCloseAlertMessage = () => {
    setAlertMessage({
      severity: "",
      msg: "",
    });
  };

  return (
    <>
      {alertMessage.msg && (
        <Alert
          severity={alertMessage.severity}
          onClose={handleCloseAlertMessage}
        >
          {alertMessage.msg}
        </Alert>
      )}
      <Box sx={{ paddingY: 2 }}>
        <Box
          sx={{
            display: display,
            width: "100%",
            backgroundColor: "#fff",
            transition: "all 500ms ease-in-out",
            boxShadow: "0 1px 3px rgba(0,0,0,0.15)",
            position: "sticky",

            top: 160,
            zIndex: 9999,
          }}
        >
          <ButtonGroup disableElevation variant="contained" sx={{ margin: 2 }}>
            <LoadingButton
              loading={generateResultsLoader}
              variant="contained"
              onClick={() => generateResults(selectedStudents)}
              disabled={selectedStudents.length === 0}
            >
              Generate Results
            </LoadingButton>
            <LoadingButton
              loading={publishResultsLoader}
              variant="contained"
              onClick={handlePublishResults}
              disabled={generatedStudents.length === 0}
            >
              Publish Results
            </LoadingButton>
          </ButtonGroup>
          {alertMessage.msg && (
            <Alert
              severity={alertMessage.severity}
              onClose={handleCloseAlertMessage}
            >
              {alertMessage.msg}
            </Alert>
          )}
        </Box>
        <MaterialTable
          icons={tableIcons}
          title={heading}
          isLoading={isLoading}
          components={{
            Toolbar: (props) => {
              return (
                <>
                  <MTableToolbar {...props} />
                  {sortComponent}
                  <ButtonGroup
                    disableElevation
                    variant="contained"
                    sx={{ margin: 2 }}
                  >
                    <LoadingButton
                      loading={generateResultsLoader}
                      variant="contained"
                      onClick={() => generateResults(selectedStudents)}
                      disabled={selectedStudents.length === 0}
                    >
                      Generate Results
                    </LoadingButton>
                    <LoadingButton
                      loading={publishResultsLoader}
                      variant="contained"
                      onClick={handlePublishResults}
                      disabled={generatedStudents.length === 0}
                    >
                      Publish Results
                    </LoadingButton>
                  </ButtonGroup>
                </>
              );
            },
          }}
          style={{
            borderTop: `solid 2px #5aa7a7`,
            boxShadow: `0px 0px 13px 0px rgb(82 63 105 / 10%)`,
            borderRadius: "10px",
            transition: "all 1000ms linear ease-in-out",
          }}
          options={{
            selection: true,
            // grouping: true,
            pageSizeOptions: [5, 10, 20, 50, 100],
            exportButton: true,
            exportMenu: [
              {
                label: "Export PDF",
                exportFunc: (cols, datas) => ExportPdf(cols, datas, heading),
              },
              {
                label: "Export CSV",
                exportFunc: (cols, datas) => ExportCsv(cols, datas, heading),
              },
            ],
          }}
          columns={[
            {
              title: "ID",
              field: "_id",
              hidden: true,
            },
            {
              title: "Index Number",
              field: "indexNumber",
            },
            {
              title: "Profile",
              field: "profile",
              render: (row) => (
                <Avatar
                  src={
                    row.profile === "" ||
                    row.profile === undefined ||
                    row.profile === null
                      ? null
                      : `${process.env.REACT_APP_BASE_URL}/images/${row.profile}`
                  }
                />
              ),
              grouping: false,
              export: false,
            },
            {
              title: "Fullname",
              field: "fullname",
            },

            {
              title: "Gender",
              field: "gender",
            },
            {
              title: "DOB",
              field: "dob",
              hidden: true,
            },
            {
              title: "Email",
              field: "email",
              width: "100px",
            },
            {
              title: "Telephone No.",
              field: "telephoneNo",
              hidden: true,
            },
            {
              title: "Department",
              field: "department",
              hidden: true,
            },
            {
              title: "Programme",
              field: "programme",
              // defaultGroupOrder: 0,
            },
            {
              title: "Level",
              field: "level",
              // defaultGroupOrder: 1,
            },

            {
              title: "Options",
              sorting: false,
              export: false,
              grouping: false,

              render: (rowData) => {
                return (
                  <Menu style={{ zIndex: 2000 }}>
                    <MenuButton
                      style={{
                        backgroundColor: "transparent",
                        outline: "none",
                        border: "none",
                      }}
                    >
                      <MoreVertSharp />
                    </MenuButton>
                    <MenuList>
                      <MenuItem
                        onSelect={() => {
                          localStorage.setItem(
                            "indexNumber",
                            rowData.indexNumber
                          );
                          customDispatch({
                            type: "currentStudentIndexNumber",
                            payload: rowData.indexNumber,
                          });

                          navigate(`/info/student-personal-info`, {
                            replace: true,
                          });
                        }}
                      >
                        <VisibilitySharp fontSize="32px" />
                        <span>View Student Info</span>
                      </MenuItem>
                      <MenuItem
                        // disabled={rowData.indexNumber !== undefined}
                        onSelect={async () => {
                          await customDispatch({
                            type: "assignIndexNo",
                            payload: rowData._id,
                          });
                          setOpenAssignIndex(true);
                        }}
                      >
                        <NumbersSharp fontSize="32px" />
                        <span>Assign Index Number</span>
                      </MenuItem>
                      <MenuItem
                        onSelect={() => {
                          navigate("/info/data-entry", {
                            state: {
                              student: {
                                id: rowData._id,
                                fullname: rowData.fullname,
                                indexNumber: rowData.indexNumber,
                                profile: rowData.profile,
                              },
                            },
                          });
                        }}
                      >
                        <DocumentScannerSharp fontSize="32px" />
                        <span>Add Results</span>
                      </MenuItem>

                      <MenuItem
                        onSelect={async () => {
                          await customDispatch({
                            type: heading,
                            payload: rowData,
                          });
                          setOpenStudentEdit(true);
                        }}
                      >
                        <Stack direction="row">
                          <Edit fontSize="32px" />
                          <span>Edit</span>
                        </Stack>
                      </MenuItem>
                      <MenuItem
                        onSelect={() => {
                          deleteInfo(rowData._id);
                        }}
                      >
                        <DeleteSharp fontSize="32px" />
                        <span>Delete</span>
                      </MenuItem>
                    </MenuList>
                  </Menu>
                );
              },
            },
          ]}
          data={data}
          actions={[
            // {
            //   position: "auto",
            //   icon: () => <NoteSharp />,
            //   tooltip: "Publish Results",
            //   onClick: (e, rowData) => {
            //     generateResults(rowData);
            //   },
            // },
            {
              position: "toolbar",
              icon: () => <RefreshSharp />,
              tooltip: "Refresh",
              isFreeAction: true,
              onClick: () => {
                refresh();
              },
            },
            {
              position: "auto",
              tooltip: "Assign Index Number",
              icon: () => <NumbersSharp fontSize="32px" />,
              onClick: async (e, rowData) => {
                await customDispatch({
                  type: "assignIndexNo",
                  payload: rowData,
                });
                setOpenAssignIndex(true);
              },
            },

            {
              position: "auto",
              tooltip: "Delete Students",
              icon: () => <DeleteSharp />,
              onClick: (e, rowData) => {
                let rowIdArr = [];
                rowData.map((row) => rowIdArr.push(row._id));
                deleteInfo(rowIdArr);
              },
            },
          ]}
          onSelectionChange={(rowData) => {
            setSelectedStudents(rowData);
          }}
        />
      </Box>
      <EditStudent open={openStudentEdit} setOpen={setOpenStudentEdit} />
      <AssignIndexNumber open={openAssignIndex} setOpen={setOpenAssignIndex} />
    </>
  );
}

export default React.memo(CustomStudentTable);
