import React, { useContext, useState, useEffect } from "react";
import { Add } from "@mui/icons-material";
import Helmet from "react-helmet";
import { useQuery } from "react-query";
import {
  Breadcrumbs,
  Button,
  Typography,
  Box,
  Divider,
  Link,
  Alert,
  Autocomplete,
  TextField,
} from "@mui/material";
import Content from "../components/Content";
import AddStudent from "../components/modals/AddStudent";

import { getAllStudents } from "../components/api/studentApi";
import { CustomContext } from "../components/context/provider/CustomProvider";
import { getAllDepartments } from "../components/api/departmentApi";
import { getAllProgrammes } from "../components/api/programmeApi";
import CustomStudentTable from "../components/tables/CustomStudentTable";

const Student = () => {
  const levelOptions = ["100", "200", "300", "400"];
  const { customState, customDispatch } = useContext(CustomContext);
  const [openAddStudent, setOpenAddStudent] = useState(false);
  const [studentErr, setStudentErr] = useState("");

  const [options, setOptions] = useState({
    department: "",
    programme: "",
    level: "",
  });

  const [depart, setDepart] = useState([]);
  const [prog, setProg] = useState([]);

  const students = useQuery(
    ["student", options],
    () => getAllStudents(options),
    {
      retry: 3,
      onSuccess: (data) => {
        customDispatch({ type: "allStudents", payload: data });
        // console.log(data);
      },
      onError: (error) => {
        // alert("Error occured while fetching data");
        if (error === undefined) {
          setStudentErr(
            `Connection failed.We cant connect to the server.Try again later`
          );
          return;
        }
        // console.log(error);
        setStudentErr(error);
      },
    }
  );

  useQuery(["department"], getAllDepartments, {
    retry: 3,
    onSuccess: (data) => {
      customDispatch({ type: "departOptions", payload: data });
    },
  });
  useQuery(["programme"], getAllProgrammes, {
    retry: 3,
    onSuccess: (data) => {
      customDispatch({ type: "progOptions", payload: data });
    },
  });

  useEffect(() => {
    let departOptions = [];
    let progOptions = [];

    customState.departmentOptions.forEach((data, index) => {
      departOptions.push(data.department);
    });
    customState.programmeOptions.forEach((data, index) => {
      progOptions.push(data.programme);
    });

    setDepart(departOptions);
    setProg(progOptions);
  }, [customState.programmeOptions, customState.departmentOptions]);

  return (
    <>
      <Helmet>
        <title>Results System | Student</title>
      </Helmet>
      <section className="student-container">
        <Content>
          <section>
            <Typography variant="h5">Students</Typography>
            <Box
              sx={{
                display: "flex",
                justifyContent: "flex-end",
              }}
            >
              <Breadcrumbs>
                <Link>Home</Link>
                <Typography>Student</Typography>
              </Breadcrumbs>
            </Box>

            <Divider />
            {students.isLoading && <Alert severity="info">Loading</Alert>}
            {studentErr && <Alert severity="error">{studentErr}</Alert>}
            <Box display="flex" justifyContent="flex-end" paddingY={4}>
              <Button
                variant="contained"
                disableElevation
                color="secondary"
                startIcon={<Add />}
                onClick={() => setOpenAddStudent(true)}
              >
                Add Student
              </Button>
            </Box>
            <Typography gutterBottom>Sort by :</Typography>
            <Box
              display="flex"
              justifyContent="flex-start"
              alignItems="center"
              flexWrap="wrap"
              gap={2}
            >
              <Autocomplete
                disablePortal
                options={depart}
                onChange={(e, value) =>
                  setOptions({
                    ...options,
                    department: value,
                  })
                }
                sx={{
                  width: 250,
                }}
                renderInput={(props) => (
                  <TextField
                    {...props}
                    label="Department"
                    size="small"
                    fullWidth
                  />
                )}
              />

              <Autocomplete
                disablePortal
                options={prog}
                onChange={(e, value) =>
                  setOptions({
                    ...options,
                    programme: value,
                  })
                }
                sx={{
                  width: 250,
                }}
                renderInput={(props) => (
                  <TextField
                    {...props}
                    label="Programme"
                    size="small"
                    fullWidth
                  />
                )}
              />

              <Autocomplete
                disablePortal
                options={levelOptions}
                onChange={(e, value) =>
                  setOptions({
                    ...options,
                    level: value,
                  })
                }
                sx={{
                  width: 250,
                }}
                renderInput={(props) => (
                  <TextField {...props} label="Level" size="small" fullWidth />
                )}
              />
            </Box>

            <CustomStudentTable
              isLoading={students.isLoading}
              heading="Student"
              data={students.data}
              refresh={students.refetch}
              // sortComponent={<Me />}
            />
          </section>
        </Content>

        <AddStudent open={openAddStudent} setOpen={setOpenAddStudent} />
      </section>
    </>
  );
};

export default Student;
