import React from "react";
import { Divider } from "@mui/material";

import _ from "lodash";
import { generateGradePoint } from "../config";

function PrintPage3({ studentInfo, studentRecord }) {
  let creditHoursList = [];
  let gradePointsList = [];

  function generateGPA() {
    const totalGradePoint = _.sum(gradePointsList);
    const totalCreditHours = _.sum(creditHoursList);
    const gpa = totalGradePoint / totalCreditHours;
    return gpa.toFixed(2);
  }

  const infoData = studentRecord?.map((record, index) => {
    return (
      <React.Fragment key={record._id}>
        <table width="100%" border="0">
          <tr
            style={{
              backgroundColor: "#5aa7a7",
              color: "#fff",
              padding: "16px",
            }}
          >
            <td>{record.year} ACADEMIC YEAR</td>
            <td>LEVEL {record.level}</td>
            <td>{record.semester} SEMESTER</td>
          </tr>
        </table>
        <table width="100%" border="0" style={{ paddingBottom: "16px" }}>
          <thead
            style={{
              backgroundColor: "#333",
              color: "#fff",
              textAlign: "left",
            }}
          >
            <tr>
              <th>Code</th>
              <th>Course</th>
              <th>Credit Hours</th>
              <th>Score</th>
              <th>Grade</th>
              <th>Grade Point</th>
            </tr>
          </thead>
          <tbody style={{ backgroundColor: "whitesmoke" }}>
            {record.results.map((info, index) => {
              const gp = generateGradePoint(info.grade);
              creditHoursList.push(info.creditHours);
              gradePointsList.push(gp);
              return (
                <tr key={info.course.id}>
                  <td>{info.course.id}</td>
                  <td>{info.course.label}</td>
                  <td>{info.creditHours}</td>
                  <td>{info.score}</td>
                  <td>{info.grade}</td>
                  <td>{gp}</td>
                </tr>
              );
            })}
          </tbody>
          <tfoot style={{ paddingBottom: "200px" }}>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td style={{ backgroundColor: "#5aa7a7", color: "#fff" }}>
                GPA-{generateGPA()}
              </td>
            </tr>
            <tr>
              <td>End</td>
            </tr>
          </tfoot>
        </table>
      </React.Fragment>
    );
  });

  return (
    <>
      <div
        style={{
          width: "100%",
          maxWidth: "600px",
          margin: "auto",
          padding: "20px",
          backgroundColor: "#fff",
        }}
        id="printPageA"
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            gap: "10px",
            padding: "16px 0",
          }}
        >
          <table
            width="100%"
            style={{
              textAlign: "center",
            }}
          >
            <tr>
              <td>
                <img
                  src="https://blogger.googleusercontent.com/img/a/AVvXsEjmgQKEPuA2OPPl0sErJ4oijrFu0fOYFIScPxZrtsOKsmN6lTTzi6CsX0TPnqzHCL44VU4ghmzmV6LScpciyOehKGuN9GH9OuIb299rBwE9bsTqmb26Ju6opaR_RkWnQarH6w8bIA9NaRBdZ-hT7UDWcgO5bcFuFneiUZAmvmPU1K998YJ7c83ZNQVHHA=s715"
                  alt="logo"
                  style={{ width: "120px", height: "120px" }}
                />
              </td>
              <td>
                <h2>AKENTEN APPIAH-MENKA UNIVERSITY</h2>
                <p>ACADEMIC AFFAIRS OFFICE</p>
                <p>TANOSO-KUMASI</p>
              </td>
            </tr>
          </table>
        </div>
        <p
          style={{
            textAlign: "center",
            color: "#fff",
            backgroundColor: "#000",
            padding: "10px 0",
          }}
        >
          OFFICIAL TRANSCRIPT FOR ACADEMIC RECORD
        </p>
        <Divider />
        <table
          width="100%"
          style={{
            textAlign: "center",
          }}
        >
          <tr
            style={{
              backgroundColor: "#5aa7a7",
            }}
          >
            <td
              style={{
                color: "#fff",
                padding: "8px",
              }}
            >
              STUDENT INFORMATION
            </td>
          </tr>
          <tr>
            <td style={{ padding: "10px 0" }}></td>
          </tr>
        </table>
        <table width="100%">
          <tr>
            <td>
              <p style={{ fontWeight: "bold" }}> Student Number </p>
              <p>{studentInfo?.indexNumber}</p>
            </td>

            <td>
              <p style={{ fontWeight: "bold" }}>Full Name</p>
              <p>{studentInfo?.fullname}</p>
            </td>

            <td>
              <p style={{ fontWeight: "bold" }}> Date of Birth </p>
              <p>{new Date(studentInfo?.dob).toLocaleDateString()}</p>
            </td>
          </tr>
          <tr>
            <td>
              <p style={{ fontWeight: "bold" }}>Gender </p>
              <p>{studentInfo?.gender}</p>
            </td>

            <td>
              <p style={{ fontWeight: "bold" }}> Programme </p>
              <p>{studentInfo?.programme}</p>
            </td>
          </tr>
        </table>
        <div style={{ width: "100%" }}>
          {studentRecord.length === 0 ? null : infoData}
        </div>
      </div>
    </>
  );
}

export default PrintPage3;
