import React, { useContext, useState, useEffect } from "react";
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from "@mui/material";
import { useMutation } from "react-query";
import { assignIndexNumber } from "../api/studentApi";
import { CustomContext } from "../context/provider/CustomProvider";

function AssignIndexNumber({ open, setOpen }) {
  const { customState } = useContext(CustomContext);
  const [indexNumber, setIndexNumber] = useState("");
  const [title, setTitle] = useState(" Assign New Index Number");

  useEffect(() => {
    if (typeof customState.assignedIndexNo === "object") {
      setTitle("Start Index Number");
    }
  }, [customState.assignedIndexNo]);

  const indexMutate = useMutation(assignIndexNumber);

  const handleSubmit = () => {
    const updatedIndexNumbers = customState.assignedIndexNo;
    let newlyCreatedIndexNumbers = [];

    if (typeof updatedIndexNumbers === "object") {
      let newIndex = indexNumber;
      updatedIndexNumbers.map((student) => {
        const data = { indexNumber: newIndex, id: student._id };
        newlyCreatedIndexNumbers.push(data);
        newIndex++;
        return data;
      });
    } else {
      newlyCreatedIndexNumbers.push({
        indexNumber: indexNumber,
        id: customState.assignedIndexNo,
      });
    }

    // console.log(newlyCreatedIndexNumbers);
    indexMutate.mutateAsync(newlyCreatedIndexNumbers, {
      onSuccess: (data) => {
        alert("Index number Updated");
        // console.log(data)
      },
    });
  };

  return (
    <Dialog fullWidth maxWidth="sm" open={open} onClose={() => setOpen(false)}>
      <DialogTitle title={title} textAlign="center">
        {title}
      </DialogTitle>
      <DialogContent>
        <Box padding={2}>
          <TextField
            label="Index Number"
            fullWidth
            value={indexNumber}
            onChange={(e) => setIndexNumber(e.currentTarget.value)}
          />
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleSubmit}>Save</Button>
        <Button onClick={() => setOpen(false)}>Cancel</Button>
      </DialogActions>
    </Dialog>
  );
}

export default AssignIndexNumber;
